@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Grechen+Fuemen&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
.dashboard {
  text-align: center;
}
.dashLogo {
  /* font-family: "Caveat", cursive; */
  font-family: "Grechen Fuemen", cursive;
  /* font-family: "Satisfy", cursive; */
  letter-spacing: 0.5vw;
  font-weight: 600;
  font-size: 3em;
  color: rgb(115, 196, 186);
}
.dashLogo-subTitle {
  font-size: 1.2em;
  font-weight: normal;
  font-family: "Caveat", cursive;
}
.navbarTitle {
  font-size: 1.5em;
  color: rgb(169, 224, 224);
}
.dashboard-home {
  margin-top: 5%;
}
.dash-para {
  margin-left: 30%;
  margin-right: 30%;
}
.dashboard-aboutme {
  margin-left: 20%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
/* .dashboard-aboutme-para {
  margin-left: 15%;
  margin-right: 15%;
} */
.skills {
  font-weight: bold;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  margin: 3%;
}
.card-container {
  padding: 2px 16px;
}
.card:hover {
  box-shadow: 0 8px 16px 0 #eebe92;
}
/* dfbfa7 */

.gallery-cell {
  width: 66%;
}

/* cell number */
.gallery-cell:before {
  display: block;
  color: white;
}
.contact {
  padding: 2%;
}
.footer {
  display: flex;
  justify-content: space-between;
}
.footer-icon {
  color: rgb(3, 211, 248);
}
.footer-social {
  padding-left: 1.5vw;
}
li {
  display: inline;
}

.Link:hover {
  color: aqua;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

/* @import url("https://fonts.googleapis.com/css2?family=Satisfy&display=swap"); */
/* @import url("https://fonts.googleapis.com/css2?family=Caveat&family=Grey+Qo&display=swap"); */
* {
  box-sizing: border-box;
}

.mp-0 {
  margin: 0%;
  padding: 0%;
}

.margin {
  margin: 2%;
  @media only screen and (max-width: 600px) {
    margin: 5%;
  }
}

.margin-left {
  margin-left: 2%;
}

.margin-right {
  margin-right: 2%;
}

.margin-top {
  margin-top: 1%;
}
@media only screen and (max-width: 1000px) {
  .margin-top {
    margin-top: 3%;
  }
}
.margin-bottom {
  margin-bottom: 1%;
}

.margin-lr {
  margin-left: 5px;
  margin-right: 5px;
}

.margin-tb {
  margin-top: 1%;
  margin-bottom: 1%;
}

.padding {
  padding: 2%;
}

.padding-left {
  padding-left: 2%;
}

.padding-right {
  padding-right: 10%;
}
.padding-top {
  padding-top: 2%;
}
.padding-bottom {
  padding-bottom: 2%;
}
.padding-lr {
  padding-left: 5px;
  padding-right: 5px;
}

.padding-tb {
  padding-top: 2%;
  padding-bottom: 2%;
}

.width {
  width: 20%;
}

.max-width {
  max-width: 100%;
}

.height {
  height: 40%;
}

.max_height {
  max-width: 100%;
}

.font {
  font-size: 1.2em;
  font-weight: 500;
  /* font-family: IBM Plex Sans, sans-serif; */
  font-family: "Caveat", cursive;
  overflow-wrap: break-word;
  letter-spacing: normal;
}

.font_content {
  content: inherit;
  white-space: normal;
  line-break: auto;
}

.line-clamp {
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.truncate {
  text-overflow: ellipsis;
  /* Needed to make it work */
  overflow: hidden;
  white-space: nowrap;
}

.flex {
  display: flex;
}

.text_center {
  text-align: center;
}

.items_center {
  align-items: center;
}

.content_center {
  justify-content: center;
}

.left {
  float: left;
}

.right {
  float: right;
}

.gradient {
  background: linear-gradient(
    to right,
    #c4d7e6 0,
    #9eb9ce 33%,
    #95ccd3 33%,
    #68b7c2 60%,
    #42c5bf 66%,
    #00b7ff 100%
  );
}

.shadow {
  text-shadow: #dfbfa7 10px;
}

.primary-color {
  color: #f6c5ad;
}
.secondary-color {
  color: #9c5f34;
}

.terary-color {
  color: #dfbfa7;
}

.primary-background-color {
  background-color: #956b4d;
}

.secondary-background-color {
  background-color: #dfbfa7;
}
a:link {
  text-decoration: none;
}

ol:hover {
  /* color: whitesmoke; */
  color: #8fddfc;
  border-radius: 10%;
}
@media only screen and (max-width: 1000px) {
  li {
    grid-template-columns: 100%;
    /* padding: 10px; */
  }
}

.hover:hover {
  box-shadow: 0 4px 12px 0 #eebe92;
}
.block {
  display: block;
}
fieldset {
  border: 5px solid rgb(178, 229, 236);
  box-sizing: border-box;
  grid-area: 1 / 1; /* first row, first column */
  width: inherit;
  margin: 2%;
}
.border {
  border: #66a5ad;
  border-radius: 5px;
  /* width: fit-content; */
}

hgroup {
  display: flex;
  flex-direction: column;
  order: -1;
}

hr {
  color: red;
}

a {
  color: #dfbfa7;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto auto;
  /* padding: 10px; */
}
@media only screen and (max-width: 1000px) {
  .grid-container {
    grid-template-columns: 100%;
    /* padding: 10px; */
  }
}

.grid-item {
  padding: 1vw;
  font-size: 1em;
  text-align: center;
}

.fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}
.btn {
  background-color: #956b4d;
  color: #eebe92;
}

/* .App {
  text-align: center;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background: linear-gradient(
    to top,
    #f5deca 0,
    #ebceb5 15%,
    #c7a689 26%,
    #bb9678 46%,
    #af886a 76%,
    #a88366 81%,
    #946a4b 100%
  );
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

